// ## Container
@mixin container() {
  margin-left: auto;
  margin-right: auto;
  width: 100%;
  max-width: map-get($container, max);
  padding-left: map-get($spacing, half);
  padding-right: map-get($spacing, half);
}

@mixin read-container() {
  @include media(">=large") {
    margin-left: auto;
    margin-right: auto;
    width: map-get($container, read);
  }
}

//## Link
@mixin global-link() {
  border-bottom: 1px dotted $accent-color;
  padding-bottom: 1px;

  &:hover {
    color: $base-color;
    border-color: $base-color;
  }
}

//## Clearfix
@mixin clearfix() {
  &::before,
  &::after {
    content: "";
    display: table;
  }

  &::after {
    clear: both;
  }
}

//## Dividers
@mixin separator($color: $black, $op: 0.05) {
  box-shadow: inset 0 1px 0 0 rgba($color, $op),
              inset 0 2px 1px -1px rgba($color, $op / 2);
}

@mixin nav-divider($direction, $op: 0.12) {
  @if $direction == "top" {
    box-shadow: inset 0 1px 0 0 rgba($white, $op),
                inset 0 1px 1px -1px rgba($white, $op / 2);
  } @else {
    box-shadow: inset 0 -1px 0 0 rgba($white, $op),
                inset 0 -1px 1px -1px rgba($white, $op / 2);
  }
}

//## Buttons
@mixin btn() {
  border-radius: 2px;
  padding: em(8px) em(24px);
  text-align: center;
}

@mixin btn-cta() {
  @include btn;
  font-size: $lead-font-size;
  transition: map-get($transition, cta);
  transition-property: background-color, box-shadow;
}

@mixin btn-bg($bg, $txt: $white, $perc: 6%) {
  background-color: $bg;
  box-shadow:
    0 0 0 1px $bg,
    0 1px 6px rgba($black, 0.117647),
    0 1px 4px rgba($black, 0.117647);

  color: $txt;
  transition: all map-get($transition, btn);

  &:hover {
    background-color: lighten($bg, $perc);
    box-shadow:
      0 0 0 1px lighten($bg, $perc),
      0 1px 6px rgba($black, 0.117647),
      0 1px 4px rgba($black, 0.117647);
  }

  &:active {
    background-color: lighten($bg, $perc * 2);
    box-shadow:
      0 0 0 1px lighten($bg, $perc * 2),
      0 1px 6px rgba($black, 0.117647),
      0 1px 4px rgba($black, 0.117647);
  }
}
